*{
  margin: 0%;
  padding: 0%;
  background-color: #F5F7F8;
}

.container{
  width: 100%;
  margin: auto;
  height: 100vh;

}

.container h1{
  text-align: center;
  margin: 30px;
  padding: 10px;
}

.container form{
   display: flex;
   flex-direction: column;
   width: 60%;
   margin: auto;
}

input,textarea,button{
  padding: 20px;
  border: 1px solid #45474B;
  margin: 20px;
}

button{
  border: none;
  cursor: pointer;
  background-color: #45474B;
  color: white;
}

.task{
  display: flex;
  width: 60%;
  margin: auto;
  align-items: center;
  justify-content: space-between;
}

.task div{
  width: 100%;
  margin: 20px;
  
}

.task span{
  color:#495E57;
}

.task button{
  border-radius: 100%;
  height: 30px;;
  width: 30px;
  font-size: 1.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: red;
}
